(function() {
    angular.module('myforum').directive('mfRetypeDirect', function mfRetypeDirect() {
        return {
            restrict: 'A',
            require: 'ngModel',
            link: function(scope, element, attrs, ngModel) {
                var isSame = function(value) {
                    var anotherValue = scope.$eval(attrs.mfRetypeDirect);
                    return value === anotherValue;
                }
                // 这一段没有搞明白是什么意思
                // ngModel.$parsers.push(function(value) {
                //     ngModel.$setValidity('same', isSame(value));
                //     return isSame(value) ? value : undefined;
                // })

                scope.$watch(function() {
                    return scope.$eval(attrs.mfRetypeDirect);
                }, function() {
                    ngModel.$setValidity('same', isSame(ngModel.$modelValue));
                })
            }
        }
    })
})();

(function() {
    angular.module('myforum').directive('mfError', mfError)

    function mfError($compile) {
        return {
            restrict: 'A',
            require: 'ngModel',
            link: function(scope, element, attrs, ngModel) {

                var subScope = scope.$new(true);
                subScope.hasError = function() {
                    return ngModel.$invalid && ngModel.$dirty;
                };
                subScope.errors = function() {
                    console.log(ngModel.$error)
                    return ngModel.$error;
                };
                var hint = $compile('<ul class="my-error-hint" ng-if="hasError()"><li ng-repeat="(name,wrong) in errors()" ng-if="wrong">{{name | error:customMessages}}</li></ul>')(subScope);
                element.after(hint);

            }
        };
    }
})();

(function() {
    angular.module('myforum').directive('mfCaptcha', function() {
        return {
            restrict: 'A',
            link: function(scope, element) {
                var changeSrc = function() {
                    element.attr('src', '/api/captcha.jpg?random=' + new Date().getTime());
                };
                changeSrc();
                element.on('click', function() {
                    changeSrc();
                })
            }
        }
    })
})();

(function() {
    angular.module('myforum').filter('error', function() {
        var messages = {
            email: '不是有效格式的邮件地址',
            required: '此项不能为空',
            same: '此项必须与上一项相同'
        };
        return function(name, customMessages) {
            var errors = angular.extend({}, messages, customMessages);
            return errors[name] || name;
        }
    })
})();

(function() {
  'use strict';

  angular
    .module('myforum')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1482592613329;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

