(function() {
  'use strict';

  angular
    .module('myforum')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });
      // 读者注册部分
    $stateProvider.state('reader',{
      url:'/reader',
      template:'<div ui-view></div>',
      abstract:true
    })
    $stateProvider.state('reader.create',{
      url:'/create',
      templateUrl:'app/reader/create.html',
      controller:'ReaderCreateController as vm',
      controllerAs:'create'
    })
    // 主题部分
    $stateProvider.state('thread',{
      url:'/thread',
      template:'<div ui-view></div>',
      abstract:true
    })
    $stateProvider.state('thread.list',{
      url:'/list',
      templateUrl:'app/thread/list.html',
      controller:'ThreadListController as vm',
      controllerAs:'thread'
    })
    $urlRouterProvider.otherwise('/');
  }

})();

